
.consumption-wrap,
.earn-wrap,
.account-wrap {
  overflow-x: hidden;
  overflow-y: auto;
}
.consumption-wrap,
.earn-wrap,
.account-wrap {
  width: 100%;
}
.consumption-wrap,
.earn-wrap,
.account-wrap {
  height: 100%;
}
.input-required {
  position: relative;
}
.input-required:before {
  position: absolute;
}
.sure-btn {
  display: block;
}
.bill-time-item .vux-cell-box:before,
.input-item .vux-cell-box:before {
  display: none;
}
.bill-time-item .week,
.input-item .week, .sure-btn {
  text-align: center;
}
.bill-time-item .weui-input,
.input-item .weui-input {
  text-align: right;
}
.sure-btn {
  cursor: pointer;
}
.bill-time-item .weui-cell__primary,
.bill-time-item .vux-cell-primary,
.bill-time-item .vux-datetime-value,
.bill-time-item p,
.bill-time-item .weui-label,
.bill-time-item .vux-label,
.input-item .weui-cell__primary,
.input-item .vux-cell-primary,
.input-item .vux-datetime-value,
.input-item p,
.input-item .weui-label,
.input-item .vux-label {
  font-size: 14px;
}
.input-required:before, .sure-btn {
  font-size: 16px;
}
.sure-btn {
  color: #fff;
}
.bill-time-item .weui-input,
.bill-time-item .vux-popup-picker-value,
.input-item .weui-input,
.input-item .vux-popup-picker-value {
  color: #999;
}
.vux-popup-picker-header-menu-right,
.dp-right {
  font-size: 16px !important;
  color: #58B7FF !important;
}
.dp-left {
  font-size: 16px !important;
  color: #828282 !important;
}
.input-warp {
  padding: 0 10px;
}
.input-required:before {
  content: '*';
  color: red;
}
.bill-time-item,
.input-item {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
}
.bill-time-item .weui-cell,
  .input-item .weui-cell {
    padding: 0 10px;
}
.bill-time-item .vux-prev-icon, .bill-time-item .vux-next-icon,
  .input-item .vux-prev-icon,
  .input-item .vux-next-icon {
    border: 1px solid #58B7FF;
    border-top: none;
    border-right: none;
}
.bill-time-item .inline-calendar td.current > span,
  .input-item .inline-calendar td.current > span {
    background-color: #58B7FF;
}
.bill-time-item .inline-calendar td.is-today, .bill-time-item .inline-calendar td.is-today.is-disabled,
  .input-item .inline-calendar td.is-today,
  .input-item .inline-calendar td.is-today.is-disabled {
    color: #58B7FF;
}
.sure-btn {
  margin: 20px 20px 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 5px;
  background-color: #bbb;
  box-shadow: 0 3px 0 0 #999;
  transition: background-color .5s;
}
.earn-wrap .sure-btn.sure-active-true {
  background-color: #69ce72;
  box-shadow: 0 3px 0 0 #13CE66;
}
.consumption-wrap .sure-btn.sure-active-true {
  background-color: #FF4949;
  box-shadow: 0 3px 0 0 red;
}



