
@charset "UTF-8";
html {
  -webkit-font-smoothing: antialiased;
}
body, h1, h2, h3, h4, h5, h6, p, dl, dt, dd, ul, ol, li, form, button, input, textarea, th, td {
  margin: 0;
  padding: 0;
}
body, button, input, select, textarea {
  font-family: "Microsoft Yahei", Arial, "宋体";
  font-size: 12px;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
}
address, cite, dfn, em, var, i {
  font-style: normal;
}
code, kbd, pre, samp {
  font-family: courier new,courier,monospace,arial,\5b8b\4f53;
}
form {
  display: inline;
}
small {
  font-size: 12px;
}
ul, ol {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
img {
  vertical-align: top;
  border: 0;
  -ms-interpolation-mode: bicubic;
}
button, input, select, textarea {
  font-size: 100%;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input, button, select, textarea {
  outline: none;
}
textarea {
  resize: none;
}
.hidden {
  display: none;
}
.hiddenImportant {
  display: none !important;
}
.home-wrap,
.container-view {
  left: 0;
}
.home-wrap,
.container-view {
  right: 0;
}
.home-wrap,
.container-view {
  top: 0;
}
.home-wrap,
.container-view {
  position: absolute;
}
body, .home-wrap,
.container-view {
  overflow: hidden;
}
body {
  background-color: #F9FAFC;
}
.home-wrap,
.container-view {
  bottom: 54px;
  background-color: #F9FAFC;
}
.home-wrap {
  bottom: 0;
}
.nav-item {
  fill: #999;
}
.nav-item.nav-home-item {
    width: 27px;
    height: 27px;
}
.nav-item.nav-account-item {
    margin-top: 4px;
    width: 20px;
    height: 20px;
}
.nav-item.nav-bill-item {
    margin-top: 3px;
    width: 22px;
    height: 22px;
}
.nav-item.nav-chart-item {
    margin-top: 5px;
    width: 23px;
    height: 23px;
}
.weui-tabbar__item span {
  color: #999 !important;
}
.weui-bar__item_on .nav-item {
  fill: #58B7FF;
}
.weui-bar__item_on span {
  padding: 0 3px;
  border-radius: 5px;
  color: #fff !important;
  background-color: #58B7FF;
}
.vux-pop-in-enter-active, .vux-pop-in-leave-active, .vux-pop-out-enter-active, .vux-pop-out-leave-active {
  will-change: transform;
  transition: all .5s;
  position: absolute;
  top: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-perspective: 1000px;
  perspective: 1000px;
}
.vux-pop-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
}
.vux-pop-in-enter, .vux-pop-out-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
}
.weui-toast__content {
  font-size: 12px !important;
  padding: 10px 5px !important;
}



