
@charset "UTF-8";
.balance-wrap, .balance-total {
  top: 50%;
}
.balance-wrap, .home-arrow {
  left: 50%;
}
.balance-title, .balance-total, .home-btn-wrap {
  left: 0;
}
.balance-title, .balance-total, .home-btn-wrap {
  right: 0;
}
.balance-total:after {
  bottom: 0;
}
.balance-wrap, .balance-title, .balance-total, .balance-total:after, .balance-icon, .home-btn-wrap, .home-arrow {
  position: absolute;
}
.home-btn-item {
  display: block;
}
.balance-wrap {
  overflow: hidden;
}
.balance-title, .balance-total, .home-btn-wrap {
  text-align: center;
}
.balance-title, .balance-total {
  font-weight: normal;
}
.home-btn-item {
  cursor: pointer;
}
.balance-title, .balance-total:after {
  font-size: 12px;
}
.home-btn-wrap {
  font-size: 14px;
}
.home-btn-wrap, .home-btn-item {
  color: #fff;
}
.home-active .balance-wrap {
  top: 35%;
}
.home-active .home-btn-wrap {
  bottom: 15%;
  opacity: 1;
}
.balance-wrap {
  width: 60%;
  transition: all 1s;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  padding-bottom: 60%;
  border-radius: 50%;
  background-color: #fff;
  border: 1px solid #e0e6ed;
}
.balance-title {
  color: #999;
  top: 30%;
}
.balance-total {
  height: 48px;
  margin-top: -24px;
  line-height: 48px;
  font-size: 24px;
  color: #58B7FF;
}
.balance-total:after {
    line-height: 42px;
    content: '(￥)';
    color: #999;
}
.balance-icon {
  top: 25%;
  left: 25%;
  width: 50%;
  height: 50%;
  fill: #ddd;
}
.home-btn-wrap {
  opacity: 0;
  transition: all .5s;
  bottom: -30%;
}
.home-btn-item {
  margin: 20px auto 0;
  width: 120px;
  height: 32px;
  line-height: 32px;
  background-color: #58B7FF;
  border-radius: 5px;
  box-shadow: 0 3px 0 0 #1D8CE0;
}
.home-arrow {
  margin-left: -10px;
  bottom: 20px;
  width: 20px;
  height: 20px;
  fill: #999;
  -webkit-animation: arrow-animate 2s ease-in-out infinite;
          animation: arrow-animate 2s ease-in-out infinite;
}
@-webkit-keyframes arrow-animate {
0% {
    bottom: 10px;
}
50% {
    bottom: 20px;
}
100% {
    bottom: 10px;
}
}
@keyframes arrow-animate {
0% {
    bottom: 10px;
}
50% {
    bottom: 20px;
}
100% {
    bottom: 10px;
}
}



