
.guide-trigger {
  font-size: 0;
}
.guide-item {
  width: 100%;
}
.guide-item {
  height: 100%;
}
.guide-trigger-item:after {
  top: 50%;
}
.guide-trigger-item:after, .guide-go {
  left: 50%;
}
.guide-wrap, .guide-item, .guide-trigger {
  left: 0;
}
.guide-wrap, .guide-trigger {
  right: 0;
}
.guide-wrap {
  bottom: 0;
}
.guide-wrap {
  top: 0;
}
.guide-trigger-item {
  position: relative;
}
.guide-item, .guide-trigger, .guide-trigger-item:after, .guide-jump, .guide-go {
  position: absolute;
}
.guide-wrap {
  position: fixed;
}
.guide-trigger-item {
  display: inline-block;
}
.guide-wrap {
  overflow: hidden;
}
.guide-trigger, .guide-go {
  text-align: center;
}
.guide-trigger-item, .guide-jump, .guide-go {
  cursor: pointer;
}
.guide-go {
  font-size: 14px;
}
.guide-go {
  color: #fff;
}
.guide-wrap {
  z-index: 999;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.guide-item {
  position: fiex;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
  repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
}
.guide-item.guide-item-active {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.guide-item img {
  width: 100%;
  height: 100%;
}
.guide-trigger {
  bottom: 30px;
}
.guide-trigger-item {
  width: 10px;
  height: 10px;
  border: 1px solid #58B7FF;
  border-radius: 50%;
  margin: 0 5px;
  transition: all .5s;
}
.guide-trigger-item:after {
    content: '';
    width: 6px;
    height: 6px;
    margin-top: -3px;
    margin-left: -3px;
    transition: all .5s;
    border-radius: 50%;
    background-color: #fff;
}
.guide-trigger-item.guide-active-trigger:after {
    background-color: #58B7FF;
}
.guide-jump {
  width: 20px;
  height: 20px;
  top: 20px;
  right: 20px;
  fill: #1296db;
  z-index: 999;
}
.guide-go {
  border-radius: 10px;
  width: 120px;
  height: 40px;
  bottom: 80px;
  box-shadow: 0 3px 0 0 #1D8CE0;
  margin-left: -60px;
  line-height: 40px;
  background-color: #58B7FF;
  z-index: 999;
}
.guide-wrap-hide {
  -webkit-animation-name: guide-fadeOutUp;
          animation-name: guide-fadeOutUp;
}
@-webkit-keyframes guide-fadeOutUp {
from {
    opacity: 1;
}
to {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
}
@keyframes guide-fadeOutUp {
from {
    opacity: 1;
}
to {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
}



