
.go-btn-box {
  top: 50%;
}
.go-btn-box {
  left: 0;
}
.go-btn-box {
  right: 0;
}
.go-btn-box {
  position: absolute;
}
.go-account {
  display: block;
}
.go-btn-box {
  text-align: center;
}
.go-account {
  cursor: pointer;
}
.go-btn-box {
  font-size: 14px;
}
.go-btn-box, .go-account {
  color: #fff;
}
.go-btn-box {
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
}
.go-account {
  margin: 0 auto 20px;
  width: 150px;
  height: 35px;
  line-height: 35px;
  border-radius: 5px;
}
.go-account.go-consumption {
    background-color: #FF4949;
    box-shadow: 0 3px 0 0 red;
}
.go-account.go-earn {
    background-color: #69ce72;
    box-shadow: 0 3px 0 0 #13CE66;
}



